import { ref } from 'vue';
export default {
  setup() {
    const YxsOption = ref("");
    const SysCascaderOption = ref([]);
    const repairDialogVisible = ref(false);
    const repairSysDialogVisible = ref(false);

    const handleClose = done => {
      ElMessageBox.confirm('确定关闭对话框?', '温馨提示', {
        type: 'info',
        center: true
      }).then(() => {
        done();
      });
    };

    return {
      YxsOption,
      SysCascaderOption,
      repairDialogVisible,
      handleClose,
      repairSysDialogVisible
    };
  },

  data() {
    return {
      // 分页
      currentPage: 1,
      pagesize: 10,
      // 搜索框
      search: "",
      // 院系所
      YxsList: [],
      Yxs: {},
      // 选择实验室
      SysList: [],
      Sys: {},
      // 设备列表
      sbList: [],
      // 选择的 设备
      Sb: {},
      // 设备故障信息
      SbgzInfo: "",
      //  实验室故障信息
      SysgzInfo: ""
    };
  },

  created() {
    this.getYxsList();
  },

  methods: {
    // 分页
    handleSizeChange: function (size) {
      this.pagesize = size;
    },
    handleCurrentChange: function (currentPage) {
      this.currentPage = currentPage;
    },

    // 获取所有院系所信息
    getYxsList() {
      this.$axios.get("/weixin-yxs/getAllYxs").then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.YxsList = data.data;
        }
      });
    },

    // 获取 设备列表
    devicelist(id) {
      this.$axios.post("/weixin-sbzk/deviceList", {
        sysh: id
      }).then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.sbList = data.data;
        } else {
          ElMessage.error("获取设备列表失败");
        }
      });
    },

    // 根据id获取实验室列表
    getSysListById(id) {
      this.$axios.get("/weixin-syszk/getById?id=" + id).then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.SysList = data.data;
        }
      });
    },

    // 选择院系所
    changeYxs() {
      this.Yxs = JSON.parse(JSON.stringify(this.YxsOption));
      this.YxsOption = this.Yxs.dwmc;
      this.SysCascaderOption = "";
      Loading.show();
      this.getSysListById(this.Yxs.dwh);
      Loading.hide();
    },

    // 选择实验室
    changeCascader(value) {
      // 获取实验室的信息 
      this.Sys = JSON.parse(JSON.stringify(this.SysList.find(t => t.sysh == value[1])));
      Loading.show();
      this.devicelist(this.Sys.sysh);
      Loading.hide();
    },

    // 修理设备弹窗
    applyRepair(vlaue) {
      this.Sb = JSON.parse(JSON.stringify(vlaue));
      this.repairDialogVisible = true;
    },

    // 修理设备 按钮 
    applyRepairButton() {
      if (this.SbgzInfo != "") {
        this.$axios.post("/weixin-sbzk/repairSb", {
          sbh: this.Sb.sbh,
          info: this.SbgzInfo,
          sbzt: "2",
          sysh: this.Sys.sysh
        }).then(res => {
          const data = res.data;

          if (data.code == 200) {
            ElMessage.success("申请成功"); // 更新设备列表

            this.devicelist(this.Sys.sysh);
            this.SbgzInfo = "";
            this.repairDialogVisible = false;
          }
        });
      } else {
        ElMessage.error("填写故障信息");
      }
    },

    applySysRepair() {
      if (Object.keys(this.Sys).length == 0) {
        ElMessage.error("请先选择实验室");
      } else {
        console.log(this.Sys);

        if (this.Sys.systype == "1") {
          ElMessage.error("该实验室已经被禁用");
        } else if (this.Sys.info != null && this.Sys.info != "") {
          ElMessage.error("该实验室已经申请过了报修");
        } else {
          this.repairSysDialogVisible = true;
        }
      }
    },

    // 实验室 设备报修按钮
    sysRepairButton() {
      if (this.SysgzInfo != "") {
        this.$axios.post("/weixin-syszk/repairSys", {
          sysh: this.Sys.sysh,
          systype: "2",
          info: this.SysgzInfo
        }).then(res => {
          const data = res.data;

          if (data.code == 200) {
            ElMessage.success("报修申请提交成功");
            this.repairSysDialogVisible = false;
            this.SysCascaderOption = []; // 获取实验室的 列表

            Loading.show();
            this.getSysListById(this.Yxs.dwh);
            Loading.hide();
          }
        }).catch(error => {
          ElMessage.error(error);
        });
      } else {
        ElMessage.error("填写故障信息");
      }
    }

  },
  computed: {
    getSysCascader() {
      let ans = [];
      var mp = new Map();
      this.SysList.map(t => {
        if (mp.has(t.sysmc)) {
          var temp = mp.get(t.sysmc);
          temp.children.push({
            value: t.sysh,
            label: t.sysmph
          });
        } else {
          var temp = {};
          temp.value = t.yxsh;
          temp.label = t.sysmc;
          temp.children = [{
            value: t.sysh,
            label: t.sysmph
          }];
          mp.set(t.sysmc, temp);
          ans.push(temp);
        }
      });
      return ans;
    },

    sbTableInfo() {
      return this.sbList.map(t => {
        if (t.sbzt == 0) {
          t['state'] = "开放";
        } else if (t.sbzt == 1) {
          t['state'] = "禁用";
        } else {
          t['state'] = "报修";
        }

        return t;
      }).filter(data => !this.search || data.sbmc.includes(this.search.toLowerCase()));
    }

  }
};